import { get, post } from '../../libs/request.js';

import {BASE_URL} from "../../config/index.js"
export default {
	data() {
		return {
			items:[
				{
					value:"1",
					name:"充不了电",
					checked:true
				},
				{
					value:"2",
					name:"功能问题",
					checked:false
				},
				{
					value:"3",
					name:"机柜问题",
					checked:false
				},{
					value:"4",
					name:"客户问题",
					checked:false
				},
				{
					value:"5",
					name:"订单问题",
					checked:false
				},
				{
					value:"6",
					name:"其他问题",
					checked:false
				}
			],
			imgList:[],
			feedback:{
				type:"1"
			}
		};
	},
	methods:{
		changed(e){
			let data = e.detail.value;
			let items = JSON.parse(JSON.stringify(this.items))
			for(let item of items) {
				item['checked'] = item.value === data
			}
			this.items = items;
			this.feedback.type = data;
		},
		uploadImg(){
			let that = this;
			let authCode = String(uni.getStorageSync("authCode"))
			uni.chooseImage({
			    count: 6, //默认9
			    sizeType: ['copressed'], //可以指定是原图还是压缩图，默认二者都有
			    sourceType: ['album'], //从相册选择
			    success: function (res) {
					let img = res.tempFilePaths[0];
					uni.uploadFile({
						url: BASE_URL + "commonn/upload",
						header:{
							authCode:authCode
						},
						filePath: img,
						name: 'file',
						success: function (uploadFileRes) {
							let data = JSON.parse(uploadFileRes.data)
							console.log(data.code)
						    if(data.code === 200){
								that.imgList.push(data.data);
							}
						}
					})
			    }
			});
		},
		delImg(index) {
			this.imgList.splice(index,1);
		},
		feedbackFn() {
			if(!this.feedback.deviceId || !this.feedback.phone || !this.feedback.remarks){
				return uni.showToast({
					title:"设备编号、备注或手机号为空",
					icon:"none"
				})
			}
			let myreg = /^[1][2,3,4,5,6,7,8,9][0-9]{9}$/;
			if(!myreg.test(this.feedback.phone)){
				return uni.showToast({
					title:"手机号格式错误",
					icon:"none"
				})
			}
			this.feedback['type'] = Number(this.feedback['type']);
			let data = Object.assign({attachment:""},this.feedback);
			for(let item of this.imgList){
				data['attachment'] += item +','
			}
			post('user/feedback', data).then(res => {
				if (res.code === 200) {
					uni.showToast({
						title:"信息提交成功",
						icon:"none"
					})
				}
			});
		}
	}
}
